import CommonCardFrame from '@/components/CommonCardFrame';
import { deleteEmptyProperty } from "../../../../utils/utils";
export default {
  components: {
    CommonCardFrame: CommonCardFrame
  },
  props: {
    searchLoading: {
      required: true,
      type: Boolean,
      default: false
    },
    refreshLoading: {
      required: true,
      type: Boolean,
      default: false
    }
  },
  data: function data() {
    return {
      orderSearchForm: {},
      bodyStyle: {
        padding: '20px 20px 0px 20px'
      },
      form: this.$form.createForm(this, {
        name: 'signFilterForm'
      })
    };
  },
  methods: {
    onSearch: function onSearch() {
      var params = this.form.getFieldsValue();
      deleteEmptyProperty(params);
      this.$emit('search', params);
    },
    onRefresh: function onRefresh() {
      this.$emit('refresh');
    },
    onAddSignUser: function onAddSignUser() {
      this.$emit('addSignUser');
    }
  }
};