import { signOrder } from '@/api/order';
export default {
  props: {
    visible: {
      type: Boolean,
      default: false
    }
  },
  data: function data() {
    return {
      form: this.$form.createForm(this, {
        name: 'userSearch'
      }),
      sign: {
        signIn: 1,
        signOut: 0
      }
    };
  },
  methods: {
    handleOk: function handleOk(e) {
      var params = this.form.getFieldsValue();
      this.updateOrder(params);
    },
    updateOrder: function updateOrder(params) {
      var _this = this;

      signOrder(params).then(function (res) {
        if (res.status) {
          _this.$message.success('操作成功');

          _this.$emit('changeRecordSuccess', params.type);

          _this.form.resetFields();
        }
      });
    }
  }
};